import React, { Component, Fragment } from "react";
import {
  Feed,
  Segment,
  List,
  // Button,
  Header,
  Divider,
  Icon,
  Container,
} from "semantic-ui-react";
import { Accordion, Card, Modal, Spinner, Button, Table } from "react-bootstrap";
import "./Clubs.css";
import "./background.css";
import {DanceClub} from './dance';
import {EC} from './ec';


class BillHistory extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeKey: 1
    };

    // this.rotate = this.rotate.bind(this);
  }

  change(i) {
    this.setState({activeKey: i})
  }

 
  render() {
    const clubs=['Aeromodelling club', 'Programming club', 'Dance club', 'Dramatics club']
    return ( 
      <div className="clubs">
        
        <div class="area" >
          <ul class="circles">
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
          </ul>
        </div >
    
        <div class="sidebar_clubs">
          {clubs.map((club,i)=> {
            return(
              <a onClick={()=>this.change(i+1)} className={this.state.activeKey-1==i && 'active'}>
                {club}
              </a>
            );
          })}
        </div>



        <Container className='clubs_cont' text>


          <Accordion className='options_accordion' defaultActiveKey="0">
            <Card>
              <Card.Header>
                {/* <span className='mat'> */}
                <span className='clubs_head'>Clubs List</span>
                {/* </span> */}
                <Accordion.Toggle id='clubs_names' 
                  onClick={function(){
                    if(document.getElementById('clubs_names').style.transform !== 'rotate(-90deg)'){
                    document.getElementById('clubs_names').style = 'transform: rotate(-90deg)'
                    } else {
                    document.getElementById('clubs_names').style = 'transform: rotate(0deg)'
                    }
                  }}
                  as={Button} variant="link" eventKey="1">
                  <Icon name='chevron circle left' />
                </Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey="1">
                <Card.Body>
                {clubs.map((club,i)=> {
                  return(
                    <>
                    <div className='names' style={{color:  `${i+1==this.state.activeKey ? '#077bc9' : ''}`}} onClick={()=>this.change(i+1)}>
                      {club}
                    </div><Divider/>
                    </>
                  );
                })}
             </Card.Body>
              </Accordion.Collapse>
            </Card>
          </Accordion>
          <div className='clubs_body'>
          {
            this.state.activeKey==1 ? <DanceClub /> :
            this.state.activeKey==2 ? <EC /> :
            this.state.activeKey==3 ? <EC /> :
            this.state.activeKey==4 ? <EC /> :
            this.state.activeKey==5 ? '' : ''

          }
          </div>
        </Container>
        
        <Segment
          inverted
          vertical
          style={{ margin: "12em 0em 0em", padding: "6em 0em" }}
        >
          <Container textAlign="center">
            <List horizontal inverted divided link size="small">
              <List.Item>Designed by Hall 2</List.Item>
            </List>
          </Container>
        </Segment>

      </div>
    );
  }
}

export default BillHistory;
